import { useState, useEffect, useMemo, useCallback } from 'react';
import { getReviewsWithStats } from '../api';

/**
 *       
 *  requestAnimationFrame   
 */
function scrollToReviewsList() {
  //  requestAnimationFrame    UI
  requestAnimationFrame(() => {
    //       
    const selectors = [
      '.reviews-list',           //   
      '.snx-reviews-widget',     //  
      '[data-snx-reviews-widget]', //  
      '.widget-summary'          //  
    ];
    
    let target = null;
    for (const selector of selectors) {
      target = document.querySelector(selector);
      if (target) break;
    }
    
    if (target) {
      //      
      const targetRect = target.getBoundingClientRect();
      const offsetTop = window.pageYOffset + targetRect.top - 20;
      
      // ,    
      if (Math.abs(window.pageYOffset - offsetTop) > 10) {
        window.scrollTo({ 
          top: offsetTop, 
          behavior: 'smooth' 
        });
      }
    } else {
      //    ,    
      window.scrollTo({ top: 0, behavior: 'smooth' });
    }
  });
}

/**
 *       CMS ID
 * @param {string} cmsId - CMS ID 
 * @param {Object} options -   API
 */
export function useReviews(cmsId, options = {}) {
  //  
  const [reviews, setReviews] = useState([]);
  const [pagination, setPagination] = useState(null);
  const [organization, setOrganization] = useState(null);
  const [stats, setStats] = useState(null);
  
  //  
  const [isLoading, setIsLoading] = useState(true);
  const [isError, setIsError] = useState(false);
  const [error, setError] = useState(null);
  const [isTransitioning, setIsTransitioning] = useState(false);
  
  //  
  const [currentFilters, setCurrentFilters] = useState({
    sortBy: 'submitted_at',
    sortOrder: 'DESC',
    hasMedia: undefined
  });
  const [currentPage, setCurrentPage] = useState(1);

  /**
   *       
   */
  const loadReviews = useCallback(async (filters = currentFilters, page = 1, isInitialLoad = false) => {
    if (!cmsId) return;

    if (isInitialLoad) {
      setIsLoading(true);
    } else {
      setIsTransitioning(true);
    }
    
    setIsError(false);
    setError(null);

    try {
      const requestOptions = {
        ...options,
        ...filters,
        limit: options.limit || 10,
        offset: (page - 1) * (options.limit || 10)
      };

      const data = await getReviewsWithStats(cmsId, requestOptions);

      setReviews(data.reviews);
      setPagination(data.pagination);
      
      //        
      if (isInitialLoad || !organization) {
        setOrganization(data.organization);
        setStats(data.stats);
      }
      
      if (isInitialLoad) {
        setIsLoading(false);
      } else {
        setIsTransitioning(false);
      }

    } catch (err) {
      console.error('[useReviews] Error loading reviews:', err);
      setIsError(true);
      setError(err.message);
      setIsLoading(false);
      setIsTransitioning(false);
    }
  }, [cmsId, options, organization]);

  /**
   *   
   */
  const applyFilters = useCallback(async (newFilters) => {
    const updatedFilters = { ...currentFilters, ...newFilters };
    setCurrentFilters(updatedFilters);
    setCurrentPage(1);
    
    //     
    await loadReviews(updatedFilters, 1, false);
    
  }, [currentFilters, loadReviews]);

  /**
   *   
   */
  const goToPage = useCallback(async (page) => {
    if (page === currentPage) return;
    
    scrollToReviewsList();
    setCurrentPage(page);
    
    //     
    await loadReviews(currentFilters, page, false);
  }, [currentPage, currentFilters, loadReviews]);

  //      
  useEffect(() => {
    if (cmsId) {
      loadReviews(currentFilters, 1, true);
    }
  }, [cmsId]); //    cmsId

  //       
  useEffect(() => {
    if (cmsId) {
      setCurrentPage(1);
      loadReviews(currentFilters, 1, false);
    }
  }, [options.limit, options.media, options.replies, options.minRating, options.maxRating, options.hasReply]); //    

  //     stats ( )
  const mediaStats = useMemo(() => {
    //      API
    if (stats && stats.totalReviewsWithMedia !== undefined) {
      return {
        totalReviewsWithMedia: stats.totalReviewsWithMedia
      };
    }
    
    // Fallback -    
    const currentWithMedia = reviews.filter(review => review.hasMedia).length;
    return {
      totalReviewsWithMedia: currentWithMedia
    };
  }, [stats, reviews]);

  return {
    reviews, //   
    pagination,
    organization,
    stats: stats ? {
      ...stats,
      ...mediaStats //   
    } : null,
    isLoading,
    isError,
    error,
    currentPage,
    isTransitioning,
    currentFilters, //   
    goToPage,
    applyFilters
  };
}
