import { ChevronLeft, ChevronRight, MoreHorizontal } from 'lucide-react';

/**
 *   
 *    UX     
 */
export default function Pagination({
  currentPage = 1,
  totalPages = 1,
  onPageChange,
  showFirstLast: _showFirstLast = true,
  maxVisiblePages = 7,
  className = ''
}) {
  //    2,   
  if (totalPages <= 1) return null;

  //      
  const getVisiblePages = () => {
    const pages = [];
    
    if (totalPages <= maxVisiblePages) {
      //      ,  
      for (let i = 1; i <= totalPages; i++) {
        pages.push(i);
      }
    } else {
      //       
      const halfVisible = Math.floor(maxVisiblePages / 2);
      
      if (currentPage <= halfVisible + 1) {
        // : 1, 2, 3, 4, 5, ..., last
        for (let i = 1; i <= maxVisiblePages - 2; i++) {
          pages.push(i);
        }
        pages.push('ellipsis-end');
        pages.push(totalPages);
      } else if (currentPage >= totalPages - halfVisible) {
        // : 1, ..., n-4, n-3, n-2, n-1, n
        pages.push(1);
        pages.push('ellipsis-start');
        for (let i = totalPages - (maxVisiblePages - 3); i <= totalPages; i++) {
          pages.push(i);
        }
      } else {
        // : 1, ..., current-1, current, current+1, ..., last
        pages.push(1);
        pages.push('ellipsis-start');
        for (let i = currentPage - 1; i <= currentPage + 1; i++) {
          pages.push(i);
        }
        pages.push('ellipsis-end');
        pages.push(totalPages);
      }
    }
    
    return pages;
  };

  const visiblePages = getVisiblePages();

  const handlePageClick = (page) => {
    if (page !== currentPage && page >= 1 && page <= totalPages) {
      onPageChange(page);
    }
  };

  return (
    <nav 
      className={`tw:flex tw:items-center tw:justify-center tw:gap-1 tw:sm:gap-2 ${className}`}
      aria-label=""
    >
      {/*  "" */}
      <button
        onClick={() => handlePageClick(currentPage - 1)}
        disabled={currentPage === 1}
        className="pagination-button pagination-nav-button"
        aria-label=" "
      >
        <ChevronLeft size={16} />
        <span className="tw:hidden tw:sm:inline tw:ml-1"></span>
      </button>

      {/*   */}
      <div className="tw:flex tw:items-center tw:gap-1">
        {visiblePages.map((page, index) => {
          if (page === 'ellipsis-start' || page === 'ellipsis-end') {
            return (
              <div
                key={`ellipsis-${index}`}
                className="pagination-ellipsis"
                aria-hidden="true"
              >
                <MoreHorizontal size={16} />
              </div>
            );
          }

          const isActive = page === currentPage;
          
          return (
            <button
              key={page}
              onClick={() => handlePageClick(page)}
              className={`pagination-button ${
                isActive ? 'pagination-button-active' : 'pagination-button-default'
              }`}
              aria-label={` ${page}`}
              aria-current={isActive ? 'page' : undefined}
            >
              {page}
            </button>
          );
        })}
      </div>

      {/*  "" */}
      <button
        onClick={() => handlePageClick(currentPage + 1)}
        disabled={currentPage === totalPages}
        className="pagination-button pagination-nav-button"
        aria-label=" "
      >
        <span className="tw:hidden tw:sm:inline tw:mr-1"></span>
        <ChevronRight size={16} />
      </button>
    </nav>
  );
}

/**
 *    
 *    
 */
export function PaginationInfo({ 
  currentPage, 
  itemsPerPage, 
  totalItems,
  className = ''
}) {
  if (totalItems === 0) return null;

  const startItem = (currentPage - 1) * itemsPerPage + 1;
  const endItem = Math.min(currentPage * itemsPerPage, totalItems);

  return (
    <div className={`tw:text-sm tw:text-slate-600 tw:text-center ${className}`}>
       <span className="tw:font-medium tw:text-slate-900">{startItem}{endItem}</span> {' '}
      <span className="tw:font-medium tw:text-slate-900">{totalItems}</span> 
    </div>
  );
}
