import { useState, useEffect, memo, useMemo, useRef, useCallback } from 'react';
import { Star, ExternalLink, Shield, MessageCircle, Globe } from 'lucide-react';
import Lightbox from 'yet-another-react-lightbox';
import 'yet-another-react-lightbox/styles.css';
import { useReviews } from '../../hooks/useReviews';
import { formatRelativeDate } from './helpers';
import YaIcon from './YaIcon';
import Pagination, { PaginationInfo } from '../Pagination';
import SmoothImage from './SmoothImage';
import FilterPanel from '../FilterPanel';
import StatsHeader from './StatsHeader';

//       
const formatTextWithLineBreaks = (text) => {
  if (!text) return '';
  
  return text.split('\n').map((line, index, array) => (
    <span key={index}>
      {line}
      {index < array.length - 1 && <br />}
    </span>
  ));
};

/**
 *     
 *   Reviews Flow Service API
 */
export default function ReviewsWidget({ 
  cmsId, 
  maxReviews = 10,
  showHeader = true,
  showFilters = true,
  includeMedia = true,
  includeReplies = true,
  showPagination = true,
  minRating,
  maxRating,
  hasReply
}) {
  const apiOptions = {
    limit: maxReviews,
    media: includeMedia,
    replies: includeReplies,
    minRating,
    maxRating,
    hasReply
  };

  const { reviews, pagination, organization, stats, isLoading, isError, error, currentPage, isTransitioning, currentFilters, goToPage, applyFilters } = useReviews(cmsId, apiOptions);

  //    ,    
  const displayedReviews = useMemo(() => reviews, [reviews]);

  //    
  const totalPages = useMemo(() => 
    pagination ? Math.ceil(pagination.total / (apiOptions.limit || 10)) : 1,
    [pagination, apiOptions.limit]
  );

  const handlePageChange = (page) => {
    goToPage(page);
  };

  //   
  const handleFiltersChange = (newFilters) => {
    applyFilters(newFilters);
  };

  //       FilterPanel
  const reviewsWithPhotos = useMemo(() => {
    if (!stats) return 0;
    return stats.totalReviewsWithMedia || 0;
  }, [stats]);

  if (isLoading) {
    return (
      <div className="tw:w-full">
        {/* Header skeleton */}
        {showHeader && (
          <div className="widget-summary tw:p-6 tw:sm:p-8 tw:mb-8 tw:sm:mb-10 tw:animate-pulse">
            <div className="tw:text-center tw:sm:text-left">
              <div className="tw:mb-6">
                <div className="tw:h-8 tw:bg-slate-200 tw:rounded-lg tw:w-48 tw:mb-2 tw:mx-auto tw:sm:mx-0"></div>
                <div className="tw:h-4 tw:bg-slate-200 tw:rounded tw:w-32 tw:mx-auto tw:sm:mx-0"></div>
              </div>
              <div className="tw:flex tw:flex-col tw:sm:flex-row tw:items-center tw:justify-center tw:sm:justify-start tw:gap-6 tw:sm:gap-10">
                <div className="tw:flex tw:items-center tw:gap-4">
                  <div className="tw:w-16 tw:h-16 tw:sm:w-18 tw:sm:h-18 tw:bg-slate-200 tw:rounded-3xl"></div>
                  <div>
                    <div className="tw:h-10 tw:bg-slate-200 tw:rounded tw:w-16 tw:mb-1"></div>
                    <div className="tw:h-4 tw:bg-slate-200 tw:rounded tw:w-20"></div>
                  </div>
                </div>
                <div className="tw:text-center tw:sm:text-left">
                  <div className="tw:h-6 tw:bg-slate-200 tw:rounded tw:w-24 tw:mb-1"></div>
                  <div className="tw:h-4 tw:bg-slate-200 tw:rounded tw:w-16"></div>
                </div>
              </div>
            </div>
          </div>
        )}

        {/* Reviews skeleton */}
        <div className="widget-spacing-lg">
          {[...Array(maxReviews)].map((_, i) => (
            <div key={i} className="widget-card tw:p-4 tw:sm:p-6 tw:animate-pulse">
              <div className="tw:flex tw:items-start tw:gap-3 tw:mb-4">
                <div className="tw:w-12 tw:h-12 tw:sm:w-14 tw:sm:h-14 tw:bg-slate-200 tw:rounded-2xl tw:flex-shrink-0"></div>
                <div className="tw:flex-1 tw:min-w-0">
                  <div className="tw:flex tw:items-start tw:justify-between tw:mb-2">
                    <div className="tw:h-5 tw:bg-slate-200 tw:rounded tw:w-32"></div>
                    <div className="tw:h-6 tw:bg-slate-200 tw:rounded-full tw:w-20"></div>
                  </div>
                  <div className="tw:flex tw:items-center tw:justify-between tw:mb-3">
                    <div className="tw:flex tw:gap-1">
                      {[...Array(5)].map((_, j) => (
                        <div key={j} className="tw:w-4 tw:h-4 tw:bg-slate-200 tw:rounded"></div>
                      ))}
                    </div>
                    <div className="tw:h-4 tw:bg-slate-200 tw:rounded tw:w-16"></div>
                  </div>
                </div>
              </div>
              <div className="tw:space-y-2 tw:mb-4">
                <div className="tw:h-4 tw:bg-slate-200 tw:rounded tw:w-full"></div>
                <div className="tw:h-4 tw:bg-slate-200 tw:rounded tw:w-5/6"></div>
                <div className="tw:h-4 tw:bg-slate-200 tw:rounded tw:w-4/6"></div>
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  }

  if (isError) {
    return (
      <div className="tw:text-sm tw:text-gray-500 tw:text-center tw:py-8">
        <div className="tw:bg-red-50 tw:border tw:border-red-200 tw:rounded-lg tw:p-4 tw:max-w-md tw:mx-auto">
          <h3 className="tw:text-red-800 tw:font-semibold tw:mb-2">  </h3>
          <p className="tw:text-red-700 tw:text-sm">
            {error || '   '}
          </p>
          {error && error.includes('API ') && (
            <div className="tw:mt-3 tw:text-xs tw:text-red-600">
              <p> :</p>
              <ul className="tw:list-disc tw:list-inside tw:mt-1 tw:space-y-1">
                <li>API     localhost:3000</li>
                <li>  CORS </li>
                <li>   </li>
              </ul>
            </div>
          )}
        </div>
      </div>
    );
  }

  if (!cmsId) {
    return (
      <div className="tw:text-sm tw:text-gray-500 tw:text-center tw:py-4">
         CMS ID    
      </div>
    );
  }

  if (!reviews.length && !isLoading) {
    return (
      <div className="tw:text-sm tw:text-gray-500 tw:text-center tw:py-4">
            
      </div>
    );
  }

  return (
    <div className="snx-reviews-widget" data-snx-reviews-widget>
      {/* Stats Header */}
      {showHeader && stats && organization && (
        <StatsHeader stats={stats} organization={organization} />
      )}

      {/* Filter Panel (optional) */}
      {showFilters && !isLoading && !isError && reviews.length > 0 && (
        <FilterPanel
          onFiltersChange={handleFiltersChange}
          totalReviews={pagination?.total || reviews.length}
          reviewsWithPhotos={reviewsWithPhotos}
          isLoading={isTransitioning}
          currentFilters={currentFilters}
          className="tw:mb-4 tw:border-b tw:border-slate-100 tw:pb-3"
        />
      )}

      {/* Reviews List */}
      <div className={`widget-spacing-lg reviews-list tw:transition-opacity tw:duration-300 ${
        isTransitioning ? 'tw:opacity-50' : 'tw:opacity-100'
      }`}>
        {displayedReviews.map((review) => (
          <ReviewCard key={review.id} review={review} />
        ))}
      </div>

      {/* Pagination */}
      {showPagination && pagination && totalPages > 1 && (
        <div className="tw:mt-8 tw:sm:mt-10 tw:space-y-4">
          {/* Pagination Info */}
          <PaginationInfo
            currentPage={currentPage}
            itemsPerPage={apiOptions.limit || 10}
            totalItems={pagination.total}
            className="tw:mb-4"
          />
          
          {/* Pagination Controls */}
          <Pagination
            currentPage={currentPage}
            totalPages={totalPages}
            onPageChange={handlePageChange}
            className="tw:justify-center"
          />
        </div>
      )}
    </div>
  );
}

//        
const ExpandedMediaGallery = memo(function ExpandedMediaGallery({ mediaAssets, preloadImages = true }) {
  const [lightboxOpen, setLightboxOpen] = useState(false);
  const [lightboxIndex, setLightboxIndex] = useState(0);

  const photoAssets = mediaAssets.filter(asset => asset.media_type === 'photo' && asset.source_url);
  
  if (photoAssets.length === 0) return null;

  //    lightbox
  const slides = photoAssets.map(asset => ({
    src: asset.source_url,
    alt: asset.alt_text || ' '
  }));

  const openLightbox = (index) => {
    setLightboxIndex(index);
    setLightboxOpen(true);
  };

  return (
    <>
      {/* Responsive grid showing ALL photos */}
      <div className="tw:grid tw:gap-2 tw:w-full">
        {/*            */}
        <div className={`tw:grid tw:gap-2 ${
          photoAssets.length === 1 ? 'tw:grid-cols-1 tw:max-w-md' :
          photoAssets.length === 2 ? 'tw:grid-cols-2 tw:max-w-lg' :
          photoAssets.length === 3 ? 'tw:grid-cols-3 tw:max-w-2xl' :
          photoAssets.length === 4 ? 'tw:grid-cols-2 tw:sm:grid-cols-4 tw:max-w-3xl' :
          'tw:grid-cols-2 tw:sm:grid-cols-3 tw:md:grid-cols-4 tw:lg:grid-cols-5 tw:xl:grid-cols-6'
        }`}>
          {photoAssets.map((asset, index) => (
            <div 
              key={asset.asset_id || index} 
              className="tw:relative tw:rounded-lg tw:overflow-hidden tw:bg-slate-100 tw:cursor-pointer tw:group tw:transition-all tw:duration-200 hover:tw:shadow-md hover:tw:ring-2 hover:tw:ring-amber-200/50"
              style={{ aspectRatio: '1' }}
              onClick={() => openLightbox(index)}
            >
              <SmoothImage
                src={asset.source_url}
                alt={` ${index + 1}`}
                className="tw:w-full tw:h-full group-hover:tw:opacity-90 tw:transition-opacity tw:duration-200"
                skeletonClassName="tw:w-full tw:h-full"
                containerType="gallery"
                preload={preloadImages && index < 12}
              />
            </div>
          ))}
        </div>
      </div>
      
      {/* Professional lightbox with mobile support */}
      <Lightbox
        open={lightboxOpen}
        close={() => setLightboxOpen(false)}
        index={lightboxIndex}
        slides={slides}
        animation={{ fade: 300, swipe: 500 }}
        carousel={{
          finite: photoAssets.length <= 3,
          preload: 2
        }}
        controller={{
          closeOnBackdropClick: true,
          closeOnPullDown: true,
          closeOnPullUp: false
        }}
        render={{
          buttonPrev: photoAssets.length <= 1 ? () => null : undefined,
          buttonNext: photoAssets.length <= 1 ? () => null : undefined,
        }}
      />
    </>
  );
});

//       
const MediaThumbnails = memo(function MediaThumbnails({ mediaAssets }) {
  const [lightboxOpen, setLightboxOpen] = useState(false);
  const [lightboxIndex, setLightboxIndex] = useState(0);
  const [photoCount, setPhotoCount] = useState(5);
  const containerRef = useRef(null);
  
  const photoAssets = mediaAssets?.filter(asset => asset.media_type === 'photo' && asset.source_url) || [];
  
  //         
  const calculatePhotoCount = useCallback(() => {
    if (!containerRef.current) return 5;
    
    const containerWidth = containerRef.current.offsetWidth;
    
    //       
    if (containerWidth >= 720) return 5; // 5 * 140px + 4 * 8px = 732px
    if (containerWidth >= 540) return 4; // 4 * 130px + 3 * 8px = 544px  
    if (containerWidth >= 380) return 3; // 3 * 120px + 2 * 8px = 376px
    return 2; // 2 * 110px + 1 * 8px = 228px
  }, []);

  //      
  useEffect(() => {
    const updatePhotoCount = () => {
      const newCount = calculatePhotoCount();
      setPhotoCount(newCount);
    };

    updatePhotoCount();
    
    const resizeObserver = new ResizeObserver(updatePhotoCount);
    if (containerRef.current) {
      resizeObserver.observe(containerRef.current);
    }

    return () => resizeObserver.disconnect();
  }, [calculatePhotoCount]);
  
  if (photoAssets.length === 0) return null;

  //    lightbox
  const slides = photoAssets.map(asset => ({
    src: asset.source_url,
    alt: asset.alt_text || ' '
  }));

  const openLightbox = (index) => {
    setLightboxIndex(index);
    setLightboxOpen(true);
  };

  //      
  const getPhotoSize = () => {
    switch (photoCount) {
      case 5: return { width: '140px', height: '140px' };
      case 4: return { width: '130px', height: '130px' };
      case 3: return { width: '120px', height: '120px' };
      default: return { width: '110px', height: '110px' };
    }
  };

  const photoSize = getPhotoSize();

  return (
    <>
      {/* Dynamic photo thumbnails based on container width */}
      <div ref={containerRef} className="tw:flex tw:gap-2 tw:flex-nowrap tw:overflow-hidden tw:mt-4">
        {photoAssets.slice(0, photoCount).map((asset, index) => (
          <div 
            key={asset.asset_id || index}
            className="tw:relative tw:rounded-lg tw:overflow-hidden tw:bg-slate-100 tw:cursor-pointer tw:group tw:shadow-sm hover:tw:shadow-md hover:tw:ring-2 hover:tw:ring-amber-200/50 tw:transition-all tw:duration-200"
            style={{ 
              aspectRatio: '1', 
              width: photoSize.width,
              height: photoSize.height,
              flexShrink: 0
            }}
            onClick={() => openLightbox(index)}
          >
            <SmoothImage
              src={asset.source_url}
              alt={` ${index + 1}`}
              className="tw:w-full tw:h-full group-hover:tw:opacity-90 tw:transition-opacity tw:duration-200"
              skeletonClassName="tw:w-full tw:h-full"
              containerType="thumbnail"
              preload={true}
            />
            {index === photoCount - 1 && photoAssets.length > photoCount && (
              <div 
                className="tw:absolute tw:inset-0 tw:bg-black/60 tw:flex tw:items-center tw:justify-center tw:cursor-pointer"
                onClick={(e) => {
                  e.stopPropagation();
                  openLightbox(index);
                }}
              >
                <span className="tw:text-white tw:font-semibold tw:text-sm">
                  +{photoAssets.length - photoCount}
                </span>
              </div>
            )}
          </div>
        ))}
      </div>

      {/* Professional lightbox for thumbnails */}
      <Lightbox
        open={lightboxOpen}
        close={() => setLightboxOpen(false)}
        index={lightboxIndex}
        slides={slides}
        animation={{ fade: 300, swipe: 500 }}
        carousel={{
          finite: photoAssets.length <= 3,
          preload: 2
        }}
        controller={{
          closeOnBackdropClick: true,
          closeOnPullDown: true,
          closeOnPullUp: false
        }}
        render={{
          buttonPrev: photoAssets.length <= 1 ? () => null : undefined,
          buttonNext: photoAssets.length <= 1 ? () => null : undefined,
        }}
      />
    </>
  );
});

const ReviewCard = memo(function ReviewCard({ review }) {
  const [isExpanded, setIsExpanded] = useState(false);
  const [isAnimating, setIsAnimating] = useState(false);
  const _expandableRef = useRef(null);
  
  //      
  const TRUNCATE_LENGTH = 280;
  const MIN_HIDDEN_LENGTH = 60; //       
  
  // ,    
  const exceedsLength = review.content.length > TRUNCATE_LENGTH;
  const hiddenLength = review.content.length - TRUNCATE_LENGTH;
  const shouldTruncate = exceedsLength && hiddenLength >= MIN_HIDDEN_LENGTH;
  
  //     
  const displayContent = isExpanded || !shouldTruncate 
    ? review.content 
    : review.content.substring(0, TRUNCATE_LENGTH) + '...';

  const guestName = review.guest?.name || review.guest?.sourceProfile?.username || '';
  const capitalizedName = guestName.charAt(0).toUpperCase() + guestName.slice(1);
  const avatar = review.guest?.avatar;

  //  /
  const toggleExpanded = () => {
    if (isAnimating) return;
    
    setIsAnimating(true);
    setIsExpanded(!isExpanded);
    
    //    300ms
    setTimeout(() => {
      setIsAnimating(false);
    }, 300);
  };

  const handleBusinessReplyClick = () => {
    if (review.hasReply && !isExpanded) {
      toggleExpanded();
      setTimeout(() => {
        const element = document.querySelector(`[data-review-id="${review.id}"] .business-reply`);
        if (element) {
          element.scrollIntoView({ behavior: 'smooth', block: 'center' });
        }
      }, 350);
    }
  };

  return (
    <div className="widget-card tw:p-4 tw:sm:p-6" data-review-id={review.id}>
      {/* Header - Compact and balanced */}
      <div className="tw:flex tw:items-start tw:gap-3 tw:mb-4">
        {/* Avatar - Modern glass effect */}
        {avatar ? (
          <img 
            src={avatar} 
            alt={capitalizedName}
            className="tw:w-12 tw:h-12 tw:sm:w-14 tw:sm:h-14 tw:rounded-2xl tw:object-cover tw:flex-shrink-0 tw:ring-2 tw:ring-white/50 tw:shadow-sm"
          />
        ) : (
          <div className="tw:w-12 tw:h-12 tw:sm:w-14 tw:sm:h-14 tw:rounded-2xl tw:bg-gradient-to-br tw:from-slate-100/80 tw:to-slate-200/80 tw:backdrop-blur-sm tw:flex tw:items-center tw:justify-center tw:text-slate-600 tw:font-semibold tw:flex-shrink-0 tw:ring-2 tw:ring-white/50 tw:shadow-sm tw:text-sm">
            {capitalizedName?.[0] || '?'}
          </div>
        )}
        
        {/* Content area - Compact layout */}
        <div className="tw:flex-1 tw:min-w-0">
          {/* Top row: Name + Source/Date */}
          <div className="tw:flex tw:items-center tw:justify-between tw:mb-2">
            <div className="tw:flex tw:items-center tw:gap-2 tw:min-w-0 tw:flex-1">
              <h3 className="tw:font-semibold tw:text-slate-900 tw:text-xl tw:sm:text-xl tw:leading-tight tw:truncate">
                {capitalizedName}
              </h3>
              {review.guest?.sourceProfile?.isVerified && (
                <Shield size={14} className="tw:text-blue-500 tw:flex-shrink-0" title=" " />
              )}
            </div>
            
            {/* Compact source and date */}
            <div className="tw:flex tw:items-center tw:gap-2 tw:ml-3 tw:flex-shrink-0">
              {review.source && (
                <Globe size={12} className="tw:text-slate-400" title={review.source} />
              )}
              <div className="tw:text-sm tw:text-slate-500 tw:font-medium">
                {formatRelativeDate(review.date)}
              </div>
            </div>
          </div>
          
          {/* Bottom row: Rating stars */}
          <div className="tw:flex tw:items-center tw:gap-0.5">
            {[1, 2, 3, 4, 5].map((star) => (
              <Star 
                key={star} 
                size={16} 
                className={`${
                  star <= review.rating 
                    ? 'tw:text-amber-500 tw:fill-amber-400' 
                    : 'tw:text-slate-300'
                } tw:transition-colors tw:duration-200`}
                strokeWidth={1.5}
              />
            ))}
          </div>
          
          {/* Source indicator with better mobile design - if needed */}
          {review.organization?.sourceName && (
            <div className="tw:flex tw:items-center tw:gap-2 tw:flex-shrink-0 tw:mb-2">
              <div className="tw:flex tw:items-center tw:gap-1.5 tw:px-2.5 tw:py-1.5 tw:bg-slate-50/80 tw:rounded-full tw:border tw:border-slate-200/40">
                {review.organization.sourceName === '.' ? (
                  <YaIcon className="tw:w-5 tw:h-5 tw:sm:w-4 tw:sm:h-4" />
                ) : (
                  <div className="tw:w-5 tw:h-5 tw:sm:w-4 tw:sm:h-4 tw:bg-slate-300 tw:rounded-sm tw:flex tw:items-center tw:justify-center">
                    <span className="tw:text-[9px] tw:sm:text-[8px] tw:font-bold tw:text-slate-600">
                      {review.organization.sourceName.charAt(0)}
                    </span>
                  </div>
                )}
                <span className="tw:text-sm tw:font-medium tw:text-slate-600">
                  {review.organization.sourceName}
                </span>
              </div>
              
              {review.organization?.externalUrl && (
                <a 
                  href={review.organization.externalUrl} 
                  target="_blank" 
                  rel="noopener noreferrer"
                  className="tw:flex-shrink-0 tw:p-1.5 hover:tw:bg-slate-100 tw:rounded-lg tw:transition-all tw:duration-200 tw:cursor-pointer"
                  title={`    ${review.organization.sourceName}`}
                >
                  <ExternalLink size={16} className="tw:text-slate-400 hover:tw:text-slate-600" strokeWidth={1.5} />
                </a>
              )}
            </div>
          )}
        </div>
      </div>

      {/* Author level and reputation - Compact */}
      {review.guest?.sourceProfile?.authorLevel && (
        <div className="tw:text-xs tw:text-slate-500 tw:mb-3 tw:flex tw:items-center tw:gap-2">
          <span className="tw:px-2 tw:py-0.5 tw:bg-slate-100/60 tw:rounded-full">
            {review.guest.sourceProfile.authorLevel}
          </span>
          {review.guest.sourceProfile.reputationScore && (
            <span className="tw:text-slate-400">
              {review.guest.sourceProfile.reputationScore} 
            </span>
          )}
        </div>
      )}

      {/* Review metadata - Compact mobile design */}
      {(review.language !== 'ru' || review.hasMedia || review.guest?.sourceProfile?.totalReviews > 1 || review.isModerated) && (
        <div className="tw:mb-4">
          <div className="tw:flex tw:flex-wrap tw:gap-1.5">
            {review.language && review.language !== 'ru' && (
              <span className="widget-badge tw:text-xs">
                 {review.language.toUpperCase()}
              </span>
            )}

            {review.guest?.sourceProfile?.totalReviews > 1 && (
              <span className="widget-badge tw:text-xs">
                 {review.guest.sourceProfile.totalReviews}
              </span>
            )}
            {review.isModerated && (
              <span className="widget-badge tw:text-xs">
                 
              </span>
            )}
          </div>
        </div>
      )}

      {/* Review content - Mobile-optimized typography */}
      <div className="tw:mb-5">
        <div className="tw:text-slate-800 tw:leading-relaxed tw:text-lg tw:sm:text-lg tw:font-normal tw:break-words tw:hyphens-auto" style={{wordBreak: 'break-word', overflowWrap: 'break-word'}}>
          {formatTextWithLineBreaks(displayContent)}
        </div>
        
        {shouldTruncate && (
          <button 
            onClick={toggleExpanded}
            disabled={isAnimating}
            className="tw:text-lg tw:sm:text-base tw:mt-3 tw:inline-flex tw:items-center tw:gap-1 tw:cursor-pointer tw:text-slate-600 hover:tw:text-slate-800 tw:font-medium tw:transition-colors tw:duration-200 tw:disabled:opacity-50 tw:disabled:cursor-not-allowed"
          >
            {isExpanded ? (
              <></>
            ) : (
              <> </>
            )}
          </button>
        )}
      </div>
      
      {/* Media Assets */}
      {isExpanded && review.mediaAssets && review.mediaAssets.length > 0 && (
        <div className="tw:mb-4">
          <ExpandedMediaGallery mediaAssets={review.mediaAssets} preloadImages={true} />
        </div>
      )}

      {/* Organization Reply - Mobile-optimized */}
      {isExpanded && review.hasReply && review.reply && (
        <div className="tw:bg-slate-50/80 tw:border tw:border-slate-200/40 tw:rounded-2xl tw:p-4 tw:mt-4 business-reply">
          <div className="tw:flex tw:gap-3 tw:mb-3">
            <div className="tw:w-8 tw:h-8 tw:bg-gradient-to-br tw:from-slate-100 tw:to-slate-200 tw:rounded-xl tw:flex tw:items-center tw:justify-center tw:flex-shrink-0 tw:shadow-sm">
              <span className="tw:text-slate-600 tw:font-bold tw:text-xs">
                {review.organization?.name?.[0] || ''}
              </span>
            </div>
            <div className="tw:min-w-0 tw:flex-1">
              <div className="tw:flex tw:items-center tw:gap-2 tw:mb-1">
                <div className="tw:font-semibold tw:text-slate-900 tw:text-sm tw:truncate">
                  {review.reply.author || ' '}
                </div>
                {review.reply.isOfficial && (
                  <Shield size={12} className="tw:text-green-500 tw:flex-shrink-0" title=" " />
                )}
              </div>
              <div className="tw:text-xs tw:text-slate-500">{formatRelativeDate(review.reply.date)}</div>
            </div>
          </div>
          <p className="tw:text-slate-700 tw:leading-relaxed tw:text-base tw:sm:text-base tw:break-words tw:hyphens-auto tw:pl-0 tw:sm:pl-11" style={{wordBreak: 'break-word', overflowWrap: 'break-word'}}>
            {formatTextWithLineBreaks(review.reply.text)}
          </p>
        </div>
      )}

      {/* Compact action indicators for collapsed state */}
      {!isExpanded && (review.hasReply || review.hasMedia) && (
        <div className="tw:mt-4 tw:pt-4 tw:border-t tw:border-slate-200/30">
          {/* Media thumbnails first - full width */}
          {review.hasMedia && (
            <div className="tw:mb-3">
              <MediaThumbnails 
                mediaAssets={review.mediaAssets}
              />
            </div>
          )}
          
          {/* Action buttons - better mobile layout */}
          <div className="tw:flex tw:flex-col tw:sm:flex-row tw:gap-2">
            {/* Business reply indicator */}
            {review.hasReply && (
              <button
                onClick={handleBusinessReplyClick}
                disabled={isAnimating}
                className="tw:inline-flex tw:items-center tw:gap-1.5 tw:text-lg tw:sm:text-base tw:text-slate-600 hover:tw:text-slate-800 hover:tw:bg-slate-50 tw:font-medium tw:cursor-pointer tw:px-4 tw:py-2.5 tw:sm:px-3 tw:sm:py-2 tw:rounded-xl tw:border tw:border-slate-200/40 tw:flex-shrink-0 tw:transition-all tw:duration-200 tw:disabled:opacity-50 tw:disabled:cursor-not-allowed"
              >
                <MessageCircle size={16} className="tw:sm:w-[14px] tw:sm:h-[14px]" />
                <span> </span>
              </button>
            )}
          </div>
        </div>
      )}
    </div>
  );
});
