import { useState, useEffect, useRef } from 'react';
import { getOptimalImageSize, createYandexSrcSet, getImageSizes } from './imageUtils';

/**
 *         
 */
export default function SmoothImage({ 
  src, 
  alt, 
  className = '', 
  skeletonClassName = '',
  containerType = 'thumbnail', // 'thumbnail', 'gallery', 'lightbox'
  onLoad,
  preload = false,
  ...props 
}) {
  const [isLoaded, setIsLoaded] = useState(false);
  const [isError, setIsError] = useState(false);
  const [isPreloading, setIsPreloading] = useState(preload);
  const imgRef = useRef(null);
  const preloadRef = useRef(null);

  //   URL     
  const optimizedSrc = getOptimalImageSize(src, containerType);
  const srcSet = createYandexSrcSet(src);
  const sizes = getImageSizes(containerType);

  //  
  useEffect(() => {
    if (!optimizedSrc || !preload) return;

    const preloadImg = new Image();
    preloadRef.current = preloadImg;
    
    preloadImg.onload = () => {
      setIsPreloading(false);
      setIsLoaded(true);
      onLoad?.();
    };
    
    preloadImg.onerror = () => {
      setIsPreloading(false);
      setIsError(true);
    };
    
    //   src  
    preloadImg.src = optimizedSrc;

    return () => {
      if (preloadRef.current) {
        preloadRef.current.onload = null;
        preloadRef.current.onerror = null;
      }
    };
  }, [optimizedSrc, preload, onLoad]);

  //     
  const handleLoad = () => {
    if (!preload) {
      setIsLoaded(true);
      onLoad?.();
    }
  };

  const handleError = () => {
    if (!preload) {
      setIsError(true);
    }
  };

  //       ,  
  if (preload && isPreloading) {
    return (
      <div 
        className={`image-skeleton ${skeletonClassName} ${className}`}
        {...props}
      />
    );
  }

  //   ,  
  if (isError) {
    return (
      <div 
        className={`tw:bg-slate-200 tw:flex tw:items-center tw:justify-center ${className}`}
        {...props}
      >
        <span className="tw:text-slate-400 tw:text-xs"> </span>
      </div>
    );
  }

  return (
    <img
      ref={imgRef}
      src={optimizedSrc}
      srcSet={srcSet}
      sizes={sizes}
      alt={alt}
      className={`image-fade-in ${isLoaded ? 'loaded' : ''} ${className}`}
      onLoad={handleLoad}
      onError={handleError}
      loading="lazy"
      {...props}
    />
  );
}
