import { useMemo } from 'react';
import { Camera } from 'lucide-react';

/**
 * Modern FilterPanel - Premium UX & Mobile-First
 * Features:
 * - Mobile-optimized touch targets
 * - Custom dropdown with smooth animations
 * - Visual feedback and micro-interactions
 * - Accessibility support (ARIA, keyboard navigation)
 * - Advanced filter states and indicators
 * - Responsive design with adaptive layouts
 */
export default function FilterPanel({
  onFiltersChange,
  totalReviews = 0,
  reviewsWithPhotos = 0,
  className = '',
  isLoading = false,
  currentFilters = {}
}) {
  const sortOptions = [
    { 
      id: 'submitted_at-desc', 
      label: ' ', 
      shortLabel: ' ',
      description: '  ',
      sortBy: 'submitted_at', 
      sortOrder: 'DESC',
      icon: ''
    },
    { 
      id: 'rating-asc', 
      label: ' ', 
      shortLabel: '',
      description: '   ',
      sortBy: 'rating', 
      sortOrder: 'ASC',
      icon: ''
    },
    { 
      id: 'rating-desc', 
      label: ' ', 
      shortLabel: '',
      description: '   ',
      sortBy: 'rating', 
      sortOrder: 'DESC',
      icon: ''
    }
  ];

  //        
  const currentSortId = useMemo(() => {
    if (currentFilters.sortBy && currentFilters.sortOrder) {
      return `${currentFilters.sortBy}-${String(currentFilters.sortOrder).toLowerCase()}`;
    }
    return 'submitted_at-desc'; // 
  }, [currentFilters.sortBy, currentFilters.sortOrder]);

  const onlyWithMedia = currentFilters.hasMedia === true;

  const currentOption = sortOptions.find(o => o.id === currentSortId);

  const handleSortChange = (sortId) => {
    if (isLoading) return;
    const option = sortOptions.find(o => o.id === sortId);
    if (!option) return;
    
    onFiltersChange({ 
      sortBy: option.sortBy, 
      sortOrder: option.sortOrder, 
      hasMedia: onlyWithMedia 
    });
  };

  const handleMediaToggle = () => {
    if (isLoading) return;
    onFiltersChange({ 
      sortBy: currentOption?.sortBy || 'submitted_at', 
      sortOrder: currentOption?.sortOrder || 'DESC', 
      hasMedia: !onlyWithMedia 
    });
  };

  return (
    <>
      {/* Desktop Layout */}
      <div className={`tw:hidden tw:sm:flex tw:items-center tw:justify-between tw:gap-4 ${className}`}>
        {/* Sort Options */}
        <div className="tw:flex tw:items-center tw:gap-1">
          <span className="tw:text-xs tw:text-slate-500 tw:mr-2">:</span>
          {sortOptions.map((option) => (
            <button
              key={option.id}
              onClick={() => handleSortChange(option.id)}
              disabled={isLoading}
              className={`
                tw:px-2 tw:py-1 tw:text-xs tw:font-medium tw:transition-colors tw:duration-200 tw:rounded
                ${currentSortId === option.id 
                  ? 'tw:bg-slate-200 tw:text-slate-900' 
                  : 'tw:text-slate-500 hover:tw:text-slate-700 hover:tw:bg-slate-100'
                }
                disabled:tw:opacity-50 disabled:tw:cursor-not-allowed
              `}
            >
              {option.shortLabel}
            </button>
          ))}
        </div>

        {/* Filter Controls */}
        <div className="tw:flex tw:items-center tw:gap-2">
          {/* Media Filter */}
          {reviewsWithPhotos > 0 && (
            <button
              onClick={handleMediaToggle}
              disabled={isLoading}
              aria-pressed={onlyWithMedia}
              aria-label={`       (${reviewsWithPhotos}  ${totalReviews})`}
              className={`
                tw:flex tw:items-center tw:gap-1.5 tw:px-2 tw:py-1 tw:text-xs tw:font-medium tw:transition-colors tw:duration-200 tw:rounded
                ${onlyWithMedia 
                  ? 'tw:bg-slate-200 tw:text-slate-900' 
                  : 'tw:text-slate-500 hover:tw:text-slate-700 hover:tw:bg-slate-100'
                }
                disabled:tw:opacity-50 disabled:tw:cursor-not-allowed
              `}
            >
              <Camera size={12} />
              <span>    </span>
              <span className={`
                tw:text-xs tw:px-1 tw:py-0.5 tw:rounded tw:font-medium
                ${onlyWithMedia 
                  ? 'tw:bg-slate-300 tw:text-slate-800' 
                  : 'tw:bg-slate-200 tw:text-slate-600'
                }
              `}>
                {reviewsWithPhotos}
              </span>
            </button>
          )}
        </div>
      </div>

      {/* Mobile Layout - Compact Right-Aligned Select */}
      <div className={`tw:sm:hidden tw:flex tw:justify-end tw:items-center ${className}`}>
        <select
          value={currentSortId}
          onChange={(e) => handleSortChange(e.target.value)}
          disabled={isLoading}
          className="tw:appearance-none tw:bg-transparent tw:border-none tw:text-blue-600 tw:text-xs tw:font-medium tw:text-right focus:tw:outline-none tw:disabled:opacity-50 tw:cursor-pointer tw:pr-4 tw:py-0.5 tw:-mr-1"
          style={{
            backgroundImage: `url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='%234f46e5' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='M6 8l4 4 4-4'/%3e%3c/svg%3e")`,
            backgroundPosition: 'right center',
            backgroundRepeat: 'no-repeat',
            backgroundSize: '14px'
          }}
        >
          {sortOptions.map((option) => (
            <option key={option.id} value={option.id}>
              {option.label}
            </option>
          ))}
        </select>
      </div>
    </>
  );
}
